html {
    height: 100%;
  }
  body {
    margin:0;
    padding:0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: #cccccc;
  }
  
  .login-box {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60%;
    max-width: 900px;
    min-width: 865px;
    height: 70%;
    max-height: 600px;
    min-height: 410px;
    padding: 0px;
    transform: translate(-50%, -50%);
    background: rgba(255, 255, 255, 0.3);
    box-sizing: border-box;
    box-shadow: 0 15px 25px rgba(0,0,0,.6);
    border-radius: 10px;
  }
  
  .login-box .img-area {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    width: 50%;
    height: 100%;
    /* background-image: url("https://www.apple.com/kr/iphone/home/bo/images/meta/iphone__ky2k6x5u6vue_og.png");  */
    background:#9ec1a3;
    background-size: cover;
    border-radius: 10px 0px 0px 10px;
    padding: 30px;
  }

  .img-area .title {
    font-weight: bold;
    margin: 0 0 30px;
    padding: 0;
    color: #fff;
    text-align: center;
  }

  .img-area .sub-title {
    text-align: center;
    margin-top: 5%;
    color: #fff;
    font-size: medium;
  }

  .login-box .input-area-login {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    width: 50%;
    height: 100%;
    background:rgba(255, 255, 255, 0.4);
    background-size: cover;
    border-radius: 0px 10px 10px 0px;
    padding: 30px;
  }
  
  .input-area-login .title {
    font-weight: bold;
    margin: 0 0 50px;
    padding: 0;
    color: #2a2a2a;
    text-align: center;
  }

  .login-box .input-area-signup {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: #dfebe1;
    background-size: cover;
    border-radius: 0px 10px 10px 0px;
    padding: 30px;
  }

  .input-area-signup .title {
    font-weight: bold;
    margin: 0 0 60px;
    padding: 0;
    color: #2a2a2a;
    text-align: center;
  }
  
  .login-box .user-box {
    position: relative;
  }
  
  .login-box .user-box input {
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    color: #2a2a2a;
    margin-bottom: 20px;
    border: none;
    border-bottom: 1px solid #2a2a2a;
    border-radius: 0;
    outline: none;
    background: transparent;
  }

  .login-box .user-box input:focus {
    outline: none;
  }

  .login-box .user-box label {
    position: absolute;
    top:0;
    left: 0;
    padding: 10px 0;
    font-size: 16px;
    color: #2a2a2a;
    pointer-events: none;
    transition: .5s;
  }
  
  .login-box .user-box input:focus ~ label,
  .login-box .user-box input:valid ~ label {
    top: -20px;
    left: 0;
    color: #2a2a2a;
    font-size: 12px;
  }
  
  .login-box form {
    width: 75%;
  }

  .login-box form a {
    position: relative;
    display: inline-block;
    padding: 10px 20px;
    color: #2a2a2a;
    font-size: 19px;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    transition: .3s;
    margin-top: 20px;
    letter-spacing: 2px;
  }
  
  .login-box a:hover {
    font-size: 28px;
    font-weight: bold;
    color: #2a2a2a;
    cursor: pointer;
  }
  
  .login-box a span {
    position: absolute;
    display: block;
  }
  
