.layout {
  min-height: 85.9vh;
}

.logo {
  width: 200px;
  height: 63px;
  text-align: center;
  vertical-align: bottom;
  pointer-events: none;
  color: white;
}

.logo-img {
  width: 200px;
  height: 63px;
}

/* header */

.header {
  padding: 0;
  background-color: rgb(240, 240, 240);
  font-size: 5vh;
  text-align: center;
  vertical-align: top;
  z-index: 1;
  top: 0;
  position: sticky;
}

.nav-alignRight {
  text-align: center;
}

.title {
  padding-left: 20px;
  text-align: left;
  font-size: 22px;
  font-weight: bold;
  color: #88aa8d;
}

/* .title a {
  color: #88aa8d;
}

.title a:hover {
  color: #b0dab7;
} */

.menu {
  text-align: left;
  background-color: rgb(240, 240, 240);
}

/* content */

.content {
  /* margin: 0 16px; */
}

.contentBox {
  padding: 24;
  min-height: 360;
  background: colorBgContainer;
  background-color: #FFFFFF;
}

.breadcrumb { 
  margin: 16px 0;
  font-weight: bold;
}

.searchBox {
  border: 2px solid #cccccc;
  border-radius: 5px;
  padding: 10px 10px 10px 10px;
  font-weight: bold;
}

.searchBox-row {
  align-items: center;
  text-align: center;
}

.searchBox-col {
  padding: 5px 0;
}

.searchBox-col-alignLeft {
  padding: 5px 0;
  text-align: left;
}

.searchBox-col-alignRight {
  padding: 5px 0;
  text-align: right;
}

.searchBox-selector {
  width: '100%';
}

/* footer */

.footer {
  text-align: center;
}

/* 로그인 */

.content-login {
  height: 300px;
  align-items: center;
  min-width: 60vh;
  text-align: center;
  vertical-align: middle;
  border: solid 1px #1e3963 ;
}

.space-align-container {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}
.space-align-block {
  flex: none;
  margin: 8px 4px;
  padding: 4px;
  border: 1px solid #40a9ff;
}
.space-align-block .mock-block {
  min-height: 78vh;
  display: inline-block;
  padding: 32px 8px 16px;
  background: rgba(150, 150, 150, 0.2);
}